body {
  background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
}

.todoApp {
  background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#inputTodo {
  background-color: rgb(226, 123, 110);
  border: none;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  color: white;
}

#addButton {
  height: 50px;
  width: 80px;
  border: none;
  background: rgba(20, 60, 170, 0.61);
  color: rgb(12, 1, 12);
  cursor: pointer;
  font-weight: bolder;
}
