.list {
  color: blanchedalmond;
  width: 300px;
  height: 50px;
  margin: 20px auto;
  border-radius: 7px;
  background-color: rgb(46, 16, 16);
  word-wrap: break-word;
}

.list p {
  position: relative;
  padding: 10px 10px;
}

#deleteButton {
  position: absolute;
  right: 10px;
  cursor: pointer;
  margin: 0 5px;
  font-size: 15px;
}

.list p input {
  background-color: transparent;
  border: none;
  font-size: 18px;
  color: blanchedalmond;
}

.list p input:focus {
  outline: none;
}
